.layout {
    display: flex;

}

.layout_navbar {
    width: 50px;
    position: fixed;
    flex: 0.5;
    background: #0c4da1;
    z-index: 10;
    height: 100vh;
    /* box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px, rgba(255, 255, 255, 0.8) -6px -2px 16px 0px; */
    transition: 0.1s ease, color 0.1s ease;
}

.layout_navbar:hover {
    transition: 0.1s ease, color 0.1s ease;
    /* width: 200px; */
    flex: 1;
}

.layout_navbar_item {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 10px;
    margin: 5px 0px;
    transition: 0.3s ease, color 0.3s ease;

}

.layout_navbar_item:hover {
    background-color: #ffffff;
    transition: 0.3s ease, color 0.3s ease;
}

.layout_navbar_item:hover .icon_home {
    color: #1773ea;
    /* transition: 0.3s ease, color 0.3s ease; */
}

.layout_container {
    margin-left: 50px;
    flex: 10;
    width: 100%;
    background-color: #fff;
}

.icon_home {
    font-size: 30px;
    align-items: center;
    text-align: center;
    margin: auto;
    color: white;
    margin: 15px 0px;
}

.cuonlen {
    width: 60px;
    height: 60px;
    background-color: rgb(23, 108, 227);
    position: fixed;
    right: 50px;
    bottom: 100px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
}

.cuonlen:hover {
    background-color: rgb(18, 83, 175);
}

.cuonlen i {
    font-size: 30px;
    color: #ddd;
}